.episodes {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;

  &__table {
    border-collapse: collapse;

    &_header {
      background-color: rgb(52, 185, 74);
      font-weight: bold;
    }

    &_body {
      background-color: #8FC748;
    }
  }

  &__filter {
    margin: 20px 0;
  }
}
